}
@media (min-width: 100em) {
  html {
    font-size: 150%;
  }
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 1.55rem;
  margin-bottom: 0;
  line-height: 1.55rem;
}
h1 {
  font-size: 1.29375rem;
  line-height: 1.7825rem;
  margin-top: 1.55rem;
  margin-bottom: 0.775rem;
}
@media (min-width: 60em) {
  h1 {
    font-size: 1.51875rem;
