.container {
  max-width: 54em;
  margin-left: auto;
  margin-right: auto;
  padding: 0 0.775rem;
}
@media (min-width: 60em) {
  .container {
    padding: 0 1.55rem;
    max-width: 48em;
  }
}
@media (min-width: 100em) {
  .container {
    max-width: 54em;
  }
}
.header {
  padding: 0.775rem 0 1.55rem 0;
}
@media (min-width: 60em) {
  .header {
    padding: 3.1rem 0;
  }
}
.main {
  padding-bottom: 3.1rem;
  overflow-wrap: break-word;
  word-wrap: break-word;
}
@media (min-width: 60em) {
  .main {
    display: flex;
  }
  .main .main__main {
    width: 34em;
  }
  .main .main__sidebar {
    width: 20em;
  }
}
.footer {
  border-top: 1px solid #eee;
  padding: 2.325rem 0 1.55rem 0;
}
@media (min-width: 60em) {
  .footer {
    padding: 2.325rem 0;
  }
}
.pad-s {
  padding: 1.55rem/2;
}
.pad-m {
  padding: 1.55rem;
}
.pad-l {
  padding: 2.325rem;
}
.pad-xl {
  padding: 3.1rem;
}
.mar-s {
  margin-top: 1.55rem;
}
.mar-m {
  margin-top: 3.1rem;
}
.mar-l {
  margin-top: 4.65rem;
}
.mar-xl {
  margin-top: 6.2rem;
}
.abstract p {
  font-size: 1.1rem;
  line-height: 1.45;
  margin-top: 0;
}
@media (min-width: 60em) {
  .abstract p {
    font-size: 1.2rem;
    line-height: 1.55;
  }
}
.keywords {
  margin-top: 1.1625rem;
  font-size: 0.9rem;
  line-height: 1.75;
  font-style: italic;
}
@media (min-width: 60em) {
  .keywords {
    font-size: 1.1rem;
    line-height: 1.5;
  }
}
.meta {
  margin-top: 2.325rem;
  padding: 1.1625rem 0;
  font-family: 'Lato', 'Gill Sans', 'Gill Sans MT', Calibri, sans-serif;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
}
.meta table {
  margin: 0;
  width: 100%;
}
@media (max-width: 40em) {
  .meta thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
}
.meta th {
  text-align: left;
  letter-spacing: 0.03rem;
  color: #999;
  font-size: 0.55rem;
  font-weight: bold;
  text-transform: uppercase;
}
@media (max-width: 40em) {
  .meta tr {
    display: flex;
    flex-wrap: wrap;
  }
}
.meta td {
  vertical-align: top;
  font-size: 0.7rem;
  line-height: 1.5;
}
@media (max-width: 40em) {
  .meta td {
    display: block;
    margin-bottom: 1.55rem;
  }
  .meta td:nth-of-type(1) {
    order: 1;
    width: 60%;
  }
  .meta td:nth-of-type(2) {
    order: 3;
    width: 60%;
  }
  .meta td:nth-of-type(3) {
    order: 5;
    width: 60%;
    margin-bottom: 0;
  }
  .meta td:nth-of-type(4) {
    order: 2;
    width: 40%;
  }
  .meta td:nth-of-type(5) {
    order: 4;
    width: 40%;
  }
  .meta td:before {
    display: block;
    margin-bottom: 0.2325rem;
    content: attr(data-label);
    letter-spacing: 0.03rem;
    color: #999;
    font-size: 0.55rem;
    font-weight: bold;
    text-transform: uppercase;
  }
}
@media (min-width: 50em) {
  .meta td {
    width: 18%;
    display: table-cell;
  }
}
@media (min-width: 50em) {
  .meta td:nth-of-type(1) {
    width: 18%;
  }
}
@media (min-width: 50em) {
  .meta td:nth-of-type(2) {
    width: 28%;
  }
}
.meta .authors,
.meta .organizations {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.meta .authors a,
.meta .organizations a {
  text-decoration: none;
}
.meta .authors li,
.meta .organizations li {
  margin: 0;
  line-height: 1.75;
}
.article__text h2:first-of-type {
  margin-top: 0;
}
.citation code {
  background-color: #f8f8f8;
  display: block;
  line-height: 1.5;
  padding: 0.2325rem 0.3875rem;
  border-radius: $radius;
  font-size: 0.6rem;
  margin-top: 0.775rem;
}
@media (min-width: 60em) {
  .citation code {
    font-size: 0.75rem;
  }
}
.reference,
.colophon {
  margin-top: 1.55rem;
}
.reference p,
.colophon p {
  margin-top: 0.2325rem;
  font-size: 0.7rem;
  color: #999;
}
@media (min-width: 60em) {
  .reference p,
  .colophon p {
    font-size: 0.8rem;
  }
}
.reference p span,
.colophon p span {
  font-weight: bold;
}
.toc {
  overflow-y: auto;
}
.toc>ul {
  overflow: hidden;
  position: relative;
}
.toc>ul li {
  list-style: none;
}
.toc-list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}
a.toc-link {
  color: currentColor;
  height: 100%;
  font-size: 0.6rem;
  font-family: 'Lato', 'Gill Sans', 'Gill Sans MT', Calibri, sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  color: #5f5f5f;
  font-weight: 200;
}
.is-collapsible {
  max-height: 1000px;
  overflow: hidden;
  transition: all 300ms ease-in-out;
}
.is-collapsed {
  max-height: 0;
}
.is-position-fixed {
  position: fixed !important;
  top: 0;
}
a.is-active-link {
  font-weight: bold;
  color: #000207;
}
.js-toc {
  display: none;
}
@media (min-width: 60em) {
  .js-toc {
    display: block;
  }
}
@supports (position: sticky) {
@media (min-width: 60em) {
    .js-toc {
      position: sticky;
      top: 35%;
    }
}
}
.js-toc li {
  line-height: 1;
  margin-bottom: 0.775rem;
}
.js-toc ul ul {
  padding-left: 0.775rem;
  margin-top: 0.3875rem;
}
